import React, {useEffect, useState} from 'react';
import { Modal, Table } from 'antd';

import { getInviteUsersById } from 'api/user';
import dayjs from 'utils/utc';
import {Link} from "react-router-dom";

const inviteColumns = [
  {title: 'ID', dataIndex: 'uid', key: 'uid', defaultSortOrder: "descend", sorter: (a, b) => a.uid - b.uid,
    render: value => <Link to={`/user/userInfo?uid=${value}`}>{value}</Link>
  },
  {title: 'Username', dataIndex: 'uname', key: 'uname'},
  {title: 'regist time', dataIndex: 'time', key: 'time',
    render: value => dayjs.unix(value).utc().utcOffset(330).format("YYYY-MM-DD HH:mm:ss")
  },
  {title: 'KYC', dataIndex: 'isKyc', key: 'isKyc', render: value => value ? "yes" : 'no'},
  {title: 'bonus', dataIndex: 'id', key: 'id',
    render: (_, record) => record.isKyc ? '30' : 10
  },
];

function InviteUser(props) {
  const { visible, uid, onCancel } = props;
  const [inviteUsers, setInviteUsers] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {

    const fetchData = async () => {
      setLoading(true);

      const invites = await getInviteUsersById(uid);
      setInviteUsers(invites);

      setLoading(false);
    };

    if (uid) {
      fetchData();
    }

  }, [uid]);

  return (
    <Modal
      title="Invite Users"
      width="80%"
      visible={visible}
      centered footer={null}
      onCancel={onCancel}
    >
      <Table
        loading={loading}
        rowKey={record => record.uid}
        scroll={{ x: true }}
        bordered
        columns={inviteColumns}
        pagination={false}
        dataSource={inviteUsers}
      />
      <p>&nbsp;</p>
    </Modal>
  );
}
export default InviteUser;
