import React, { useState } from "react";
import Page from "../../components/Page";
import ListPage from "../../components/ListPage";
import dayjs from "../../utils/utc";
import axios from "../../utils/axios";
import { Link } from "react-router-dom";

import InviteUser from "./Users";
import { Button } from "antd";

function Index(props) {
  const [recordData, setRecord] = useState([]);
  const [total, setTotal] = useState(30);
  const [modal, setModal] = useState({});

  const fetchData = async (params) => {
    let data = await axios.get("/activity/inviteRank", { params });
    if (data && data.code === 0) {
      setTotal(data.data.total | 0);
      setRecord(data.data.data);
    }
  };

  const columns = [
    { title: "Number", dataIndex: "key" },
    {
      title: "UID",
      dataIndex: "master",
      render: (uid) => <Link to={`/user/userInfo?uid=${uid}`}>{uid}</Link>,
    },
    { title: "昵称", dataIndex: "uname" },
    {
      title: "注册时间",
      dataIndex: "reg_time",
      render: (value) =>
        dayjs.unix(value).utc().utcOffset(330).format("YYYY-MM-DD HH:mm:ss"),
    },
    { title: "今日邀请人数", dataIndex: "today" },
    { title: "今日KYC认证", dataIndex: "today_kyc" },
    { title: "今日奖励金额", dataIndex: "today_bonus" },
    { title: "总邀请人数", dataIndex: "total" },
    { title: "总KYC认证", dataIndex: "kyc" },
    { title: "总奖励金额", dataIndex: "bonus" },
    {
      title: "总奖励金额",
      dataIndex: "master",
      render: (uid) => {
        return (
          <Button
            type="link"
            onClick={() => setModal({ uid: uid, visible: true })}
          >
            invite users
          </Button>
        );
      },
    },
  ];

  return (
    <Page title="邀请数据排行">
      <ListPage
        DividerShow={false}
        columns={columns}
        fetchApi={fetchData}
        data={recordData}
        total={total}
      />

      <InviteUser
        onCancel={() => setModal({ ...modal, visible: false })}
        uid={modal.uid}
        visible={modal.visible}
      />
    </Page>
  );
}

export default Index;
